import "./style.css"
import * as d3 from 'D3'

const margin = {
    top: 10,
    right: 30,
    bottom: 30,
    left: 60
}

const sizes = {
    width: 800 - margin.left - margin.right,
    height: 600 - margin.top - margin.bottom
}

d3.select("cars-graph")
    .append("svg")
        .attr("width", sizes.width + margin.left + margin.right)
        .attr("height", sizes.height + margin.top + margin.bottom)
    .append("g")
        .attr("transform",
              "translate(" + margin.left + "," + margin.top + ")")

d3.csv("./static/car-sample.csv", () =>
{
    console.log("loaded")
})

/**
 * Learning Notes
 * 
 * appending g in the svg is so you can group all the elements
 */